import React from 'react';
import {Container, Row, Col, Table} from 'reactstrap';

import './FeaturesSection.css';

const VERSIONS_JSON_URL = 'https://raw.githubusercontent.com/HyperiumClient/Hyperium-Repo/master/installer/versions.json';
const versionComparator = (versionOne, versionTwo) => versionOne["release-id"] - versionTwo["release-id"];

class DownloadsSection extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      latestInstallerVersion: undefined
    };
  }

  componentDidMount() {
    this.setState({loading: true});
    fetch(VERSIONS_JSON_URL).then(r => r.json()).then(result => this.setState({
      latestInstallerVersion: result.versions.sort(versionComparator)[0]
    })).catch(console.error).finally(() => this.setState({loading: false}));
  }

  render() {
    const { loading, latestInstallerVersion } = this.state;

    return <section id="downloads">
      <Container>
        <Row className="justify-content-center">
          <Col xs="8" sm="4" md="3" className="text-center">
            <h2>Downloads</h2>
            <hr/>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col xs="12" sm="10" md="8">
            <Table>
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Version</th>
                  <th>Links</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <th>Hyperium Client Installer</th>
                  <td>{loading ? 'Loading...' : latestInstallerVersion.name}</td>
                  <td>{loading ? 'Loading...' : <a href={latestInstallerVersion.url}>.jar</a>}</td>
                </tr>
                <tr>
                  <th>Hyperium Addon Kit</th>
                  <td>1.0</td>
                  <td>
                    <a href="https://github.com/HypixelCommunityClient/Addon-Workspace/archive/v1.0.zip">.zip</a>
                    &nbsp;
                    <a href="https://github.com/HypixelCommunityClient/Addon-Workspace/archive/v1.0.tar.gz">.tar.gz</a>
                  </td>
                </tr>
                <tr>
                  <th>Hyperium Pack</th>
                  <td>1.0</td>
                  <td><a href="http://bit.ly/HyperiumPack">Pack</a></td>
                </tr>
              </tbody>
            </Table>
          </Col>
        </Row>
      </Container>
    </section>;
  }

}

export default DownloadsSection;



